@import url(https://fonts.googleapis.com/css2?family=Poppins:wght@400;500;600&display=swap);
h2,
h3,
h4,
h5,
h6,
p,
body {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: "Poppins", sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}
a {
  color: #000;
  text-decoration: none;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

@font-face {
  font-family: "Google Sans Regular";
  src: url(/static/media/GoogleSans-Regular.6c1be5ca.ttf) format("TrueType");
}
@font-face {
  font-family: "Google Sans Medium";
  src: url(/static/media/GoogleSans-Medium.2ecb2226.ttf) format("TrueType");
}
@font-face {
  font-family: "Google Sans Bold";
  src: url(/static/media/GoogleSans-Bold.8d814c21.ttf) format("TrueType");
}
@font-face {
  font-family: "Google Sans Bold";
  src: url(/static/media/Volkhov-Regular.345adbef.ttf) format("TrueType");
}
@font-face {
  font-family: "Google Sans Bold";
  src: url(/static/media/Volkhov-Bold.c0122719.ttf) format("TrueType");
}
*,
*::after,
*::before {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}
body {
  overflow-x: hidden;
}

